import React from 'react'
import '../css/Header.css'

function Header() {
    return (
        <div className="header">
            <div className="header__position">
                <div className="header__size">
                    <div className="header__flex">
                        <div className="placeholder"></div>
                        <div className="header__flex1 OrderOnline">
                            <h1 className="headerH1">Order</h1>
                        </div>
                        <div className="header__flex2 Shop">
                            <h1 className="headerH1">Shop</h1>
                        </div>
                        <div className="header__flex3 story">
                            <h1 className="headerH1">Story</h1>
                        </div>
                        <div className="header__flex4 visit">
                            <h1 className="headerH1">Visit</h1>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Header
